.site {
    background:  url('../../../wp-content/themes/hua/img/om.png'); 
}




.site-content {
    
    max-width: 1500px;
    margin: 0 auto 2% auto;
    width:95%;
   
    
}


.page-template-fullwidth .site-content {
    
    max-width: 1000px;
    margin: 0 auto 2% auto;
    width:95%;
   
    
}


.page-template-elementor_header_footer .site-content {
    
        margin: 0 auto 0 auto !important;
         
}





.site-main {
    width: 66%;
    float: left;
    
}

 
.page-template-fullwidth .site-main, .single-program .site-main   {
    
    width: 100%;
    float: none;
   
    
}

.single-program .site-main {
    max-width:1200px;
    margin:0 auto;
}

.page-template-fullwidth .entry-title {
    text-align: center
}
#secondary {width:30%; float: right;margin:1.5%}

#secondary .widget-title {
    text-transform: uppercase;
}
#secondary ul {
    margin:0;
    font-size: 1.5rem;
    font-weight:200;
}
#secondary ul ul {
    margin:0;
    font-size: 1rem;
    font-weight:200;
}

#secondary ul ul li {
    list-style-type: square;
}


#secondary ul a {
    text-decoration: none;
}

#secondary .button {
     
    background: #F2A244;
    color: rgba(0, 0, 0, 0.8);
    display: block;
    width:100%;
     margin:0.5em auto;
    line-height: 1;
    padding: .4em 1em .4em;
    text-align: center;
     text-decoration: none;
     font-size: 20px;
}

#secondary .button:hover  {
   background: $color__background-button-hover;
}

#secondary .widget-title {
    color:#fff;
}
.entry-title {
     
    margin-top:2em;
    text-align: center;
    text-transform: uppercase;

}

.entry-content {
    background: rgba(243, 228, 209, 0.82);
    padding:3em;
 }

#secondary {
    background: #280A41;
    color: #fff;
    width:30%;
    padding:3em;
    
}

#secondary a {
    color:#fff;
}
.entry-header {
    
}

#custom-bg {
    height:100vh;
    display:flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center center;
      width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

#custom-bg h1 {
    margin:0;
    padding:1em;
    width:auto;
    background: #fafafacc;
    color: #111;
    border-radius: 15px;
    text-transform: uppercase;
    text-align:center;
    margin:1em;
}


@media screen and (max-width: 1200px) {

#secondary {padding:1em;}
}

@media screen and (max-width: 1048px) {

.eael-tabs-nav {font-size:10px;}
}


@media screen and (max-width: 900px) {
  .site-main {
    width: 100%;
    float: none;
    
}

#secondary {width:100%; float: none;margin:0 0 2% 0;}
}

@media screen and (max-width: 767px) {
 .eael-tabs-nav {font-size:15px;}
}




.page-id-121 .site {
     background:  url('../../../wp-content/themes/hua/img/holi.jpg') center center / cover no-repeat;
}